module.exports = {"t1":"Mit der Kraft des Windes","t2":"... finden wir Lösungen für die meisten Probleme und gehen mit Optimismus an alles heran, was sich dreht und (noch) nicht dreht. Die Ideen für Neues gehen uns nicht aus. Mein Team und ich haben vielfach bewiesen, dass wir (fast) jede Hürde nehmen und weiter kommen als manch anderer. Windenergie - das ist für mich mehr als nur ein Job.","t3":"Manuel Lasse","t4":"Geschäftsführung\nDipl. Ing (FH) für Umwelttechnik und Erneuerbare Energien, seit 2006 in Sachen Windenergie unterwegs","t5":"Green Wind Operations","t6":"... ist ein Unternehmen der Windenergie mit Sitz in Berlin-Mitte und Projekten in ganz Deutschland. Wir agieren selbständig und unabhängig. Wir geben volle Unterstützung in der technischen und kaufmännischen Betriebsführung für private und institutionelle Windparkbetreiber. Wir arbeiten teamorientiert und partnerschaftlich und sind seit vielen Jahren fest verankert in der Windenergie. Zertifiziert nach ISO 9001:2015.","b1":"Leistungen","t7":"Frisches Windwissen","t8":"Bei Green Wind Operations paart sich frisches Windwissen mit gereifter Erfahrung. Die Überzeugung, jede Hürde zu nehmen, verbindet sich mit Begeisterung, wenn sich die Rotoren reibungslos drehen.","b2":"Standorte","t9":"Das Beste für Sie","t10":"Wir behaupten nicht, dass wir alles besser können. Aber wir sagen, dass wir das Allerbeste für unsere Kunden herausholen - aus Maschinen, Technolgien, Verträgen, Gesetzen und aus uns. Unser Team verfügt über die Kapazitäten und Kompetenzen, Windenergieanlagen nach Inbetriebnahme rund um die Uhr bestmöglich zu betreuen.","b3":"Team","t11":"Am Puls der Energiewende","t12":"Dank unseres Standorts Berlin - direkt am politischen Puls der Energiewende - und unserer sachkundigen MitarbeiterInnen sind wir immer bestens informiert. Ob Repowering, Stromhandel, gesetzliche Grundlagen, SDL Upgrades oder Neuerungen im Bereich des EEG - wir beraten Sie aktiv und geben Empfehlungen.","b4":"Kontakt","t13":"","t14":"","t15":"","t16":"","t17":"","t18":"","t19":""}

