module.exports = {"t1":"Unser Team\nEiner für Alle. Alle für Sie!","t2":"Es gibt viele gute Gründe, sich in Sachen kaufmännischer und technischer Betriebsführung für Green Wind zu entscheiden. Jedes Mitglied unseres Teams ist ein guter Grund. Und alle zusammen sind wir mehr als die Summe Einzelner.","team":[{"name":"Manuel Lasse","job":"Geschäftsführung","bio":"studierte Umwelttechnik und Erneurbare Energien und ist seit 2006 in Sachen Windenergie unterwegs.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505141767/Manu_Lasse_250_nvxkgv.jpg"},{"name":"Andreas Texheimer","job":"technische und kaufmännische Betriebsführung","bio":"mit Genauigkeit und dem Fokus auf Safety first.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505141768/Andreas_Texheimer_250_nzbrbq.jpg"},{"name":"Dietmar Mahler","job":"technische Betriebsführung","bio":"im persönlichen Kontakt mit den Menschen vor Ort.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505141768/Dietmar_Mahler_250_ljvlzb.jpg"},{"name":"Franziska Brüll","job":"technische und kaufmännische Betriebsführung","bio":"mit der Sicht auf das große Ganze.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505141767/Franziska_Bruell_250_qeonht.jpg"},{"name":"Anne Pfannes","job":"kaufmännische Betriebsführung","bio":"mit Akribie, dem Sinn für Zahlen und neuen Ideen.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505141768/Anne_Pfannes_250_woqu6v.jpg"},{"name":"Max Horn","job":"technische Betriebsführung","bio":"mit Begeisterung für die regenerativen Energien.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505141768/Max_Horn_250_vynxjk.jpg"},{"name":"Robert Kadereit","job":"technische Betriebsführung/Leitwarte","bio":"in Windeseile am Ort des Geschehens.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505141769/Robert_Kadereit_250_n1shkm.jpg"},{"name":"Jasmin Horstkötter","job":"Buchhaltung","bio":"mit frischem Wind und der richtigen Einstellung.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505141769/Jasmin_Horstkoetterr_250_i9fwoh.jpg"},{"name":"Philipp Grytz","job":"technische Betriebsführung, Regionalbüro Groß Pankow","bio":"stets am Optimum des technisch Möglichen.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505141768/Philipp_Grytz_250_feghaw.jpg"},{"name":"Michael Walther-Schwartz","job":"technische Betriebsführung, Regionalbüro Groß Pankow","bio":"für jede Herausforderung zu haben.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505141768/Michael_Witschas_250_mmlrqh.jpg"}]}

