module.exports = {"t1":"Leitwarte 24/7","t2":"Die gesamten Abläufe der technischen Betriebsführung von Windenergieanlagen und Umspannwerken werden im Wesentlichen in unseren Leitstellen 24/7 abgebildet. Hauptleitwarte und Herz ist Berlin. Wir dokumentieren, systematisieren, überwachen und kontrollieren den gesamten Anlagenbetrieb und -zustand. Unsere Einsatzteams und deren Aktivitäten können so optimal gesteuert und effizient am Ort des Geschehens eingesetzt werden. Alle Kernprozesse werden zentral überwacht und koordiniert – 24 Stunden am Tag, sieben Tage die Woche.\n\nEffektiv und sicher.","t3":"","t4":"Technische Betriebsführung","t5":"","t6":"","sections1":[{"title":"Checken was läuft","p":"Wir inspizieren regelmäßig die Anlagen - vom Fahrkorb bis zum Maschinenhaus, kontrollieren und dokumentieren minutiös, ob Prüfungsintervalle und Wartungsaufgaben eingehalten werden. Dazu gehört auch die halbjährliche Begehung der kompletten WEA. Über alle Vorgänge sind unsere Kunden immer im Bilde, dank kundeneigenem Webzugang, individueller Berichterstellung und persönlicher Berichterstattung."},{"title":"Genau auf den Punkt","p":"Erst, wenn die technische Arbeit vor Ort und die Auswertung von Daten, Gutachten und Inspektionen, das Einholen von Angeboten sowie die detaillierte Untersuchung eines Objektes zusammenkommen und kompetent verbunden werden, erzielen wir optimale Ergebnisse. Erst dann entwickelt eine Technische Betriebsführung ihre volle Kraft. In diesem Sinne koordiniert Green Wind Operations auch externe BetriebsführerInnen in ganz Deutschland und dem benachbarten Ausland, schafft einheitliche Strukturen und bündelt Informationsflüsse."},{"title":"Rund um die Uhr überwacht","p":"Wir verwenden für die Datenüberwachung das bewährte Programm ROTORSOFT der Firma Drehpunkt sowie von der Firma Softenergy die Software WIS, d. h.:\n\n• 24 Stunden Live-Überwachung der Windenergieanlagen\n• Software gestützte Schnittstelle zur kfm. Betriebsführung\n• ideale Voraussetzung zum Stromhandel\n\nDie individuelle Berichterstellung ist abgestimmt auf Ihr Portfolio durch Monats• und Jahresberichte. Zusätzlich erstellen wir kfm. Betriebsführungsberichte."},{"title":"Überwachung externer Dienste","p":"Vertrauen ist gut, Kontrolle ist besser! Die Arbeiten externer Dienstleister werden stetig kontrolliert, Vertragsabsprachen und Abrechnungen geprüft. Wir organisieren und setzen Nebenbestimmungen gemäß BiMSchG um."}],"t9":"Kaufmännische Betriebsführung","t10":"Kaufmännische Betriebsführung bei Green Wind heißt, eine stets fließende, finanzielle Bewegung zu garantieren, alle Störfaktoren und unnötigen Kosten auszuschließen, um den bestmöglichen Ertrag herauszuholen. Schon im Vorfeld können wir das gesamte Portfolio im Hinblick auf bestehende Verträge prüfen und Vorschläge für eine sinnvolle Vertragsstruktur unterbreiten, z.B. zur Optimierung von Wartungsverträgen.","sections2":[{"title":"Fakten optimieren","p":"Das Herzstück einer Kaufmännischen Betriebsführung ist eine hieb- und stichfeste Buchhaltung nebst Abwicklung des Zahlungsverkehrs. Darüberhinaus optimiert unser Financial Management den buchhalterischen Prozess so, dass er auf den Betrieb jeder einzelnen Anlage zugeschnitten ist, angepasst an die spezifischen Finanzierungsstrukturen und die steuerlichen Gegebenheiten. Unsere MitarbeiterInnen errechnen die beste Anlagen-Effizienz und kümmern sich um eine reibungslose Abrechnung sowie Verteilung der Einspeiseerlöse."},{"title":"Prozesse planen und steuern","p":"Zur Planung und Steuerung dieser Prozesse nutzen wir unser frisches Windwissen in Sachen Liquiditäts- und Budgetplanung, Abrechnung von NSM-Ausfällen, Direktvermarktung, Commercial Due Diligence etc. Und alles basiert auf dem stetigen Kontakt zum Kunden."}],"t11":"Repowering vs. Optimierung","t12":"Die technologische Entwicklung von Windenergieanlagen hat in den letzten Jahren enorme Fortschritte gemacht – sowohl was die Effizienz, die Umweltverträglichkeit als auch die Möglichkeit anbelangt, unterschiedlichste Windstandorte bestmöglich zu nutzen. Unsere BetriebsführerInnen kennen die Anlagen genau und machen Vorschläge für Betriebsoptimierungen oder auch Repoweringmaßnahmen.","sections3":[{"title":"Laufzeit weitere 20 Jahre","p":"Für viele Windprojekte, die vor mehr als zehn Jahren erbaut wurden, lohnt sich ein Repowering - also der Ersatz einer älteren und kleineren Anlage durch eine neue. Die Laufzeit eines bestehenden Projektes kann somit um weitere 20 Jahre verlängert, die Effizienz um das drei- bis fünffache gesteigert werden. Da kann sich eine Investition schnell amortisieren."},{"title":"Lohnt sich Repowering?","p":"Wir sehen es als unsere Aufgabe an, unseren Kunden vorausschauend und aktiv Vorschläge zum Repowering zu machen und fachkundig zu ermitteln, ob sich ein Repowering tatsächlich lohnt. Erst wenn alle Fakten auf dem Tisch liegen, muss sich der Betreiber entscheiden."},{"title":"Projektierung mit Partner","p":"Unsere Partnerfirma Green Wind Energy GmbH hilft bei der Umsetzung einer Repowering-Maßnahme – von der Voruntersuchung und den Genehmigungsverfahren über die Umsetzung bis hin zum Betrieb. Green Wind Energy entwickelt seit Jahren mit Partnern Windparks in der Region und steuert Repowering-Prozesse. Wenn gewünscht, kann die Green Wind Operations GmbH anschließend die technische und kaufmännische Betriebsführung in gewohnter Qualität nahtlos übernehmen."}]}

