module.exports = {"t1":"Standorte","t2":"","sections":[{"title":"","subsections":[{"title":"Green Wind-Regionalbüro","text":"Technische Betriebsführung am Standort Groß Pankow. Vom Green Wind Regionalbüro aus werden insgesamt um die 180 Megawatt in der Prignitz überwacht, u. a. das Green Wind-Repoweringprojekt Windpark Vormark/Prignitz.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505155131/GW_Karte_2017_xxkyhz.svg"}]}],"t7":"","t8":"","t9":"","t10":"","t11":"","t12":"","t13":"","t14":"","t15":"","t16":"","t17":"","t18":"","t19":"","t20":"","t21":"","t22":"","t23":"","t24":"","t25":"","t26":"","t27":"","t28":"","t29":"","t30":"","t31":"","t32":""}

