module.exports = {"t1":"Med vindens kraft","t2":"... udvikles vores samfund. Hos Green Wind Energy bidrager vi til at forme og sikre hele vores fremtid. Jeg er fascineret af vindens kraft!","t3":"Martin Kühl","t4":"Administrerende direktør\nshar arbejdet med vindenergi siden 1995","t5":"Green Wind Energy","t6":"... er en vindenergivirksomhed med hovedsæde i Berlin centrum og projekter i hele Tyskland. Vi opererer selvstændigt og uafhængigt. Vi giver fuld støtte til projektudvikling af vindenergianlæg til private og institutionelle operatører af vindparker. Vi arbejder med fokus på samarbejde og partnerskab og har i mange år været fast forankret i vindenergien. Certificeret iht.","b1":"Ydelser","t7":"Ny viden om vind","t8":"Hos Green Wind Energy kombineres ny viden om vind med mange års erfaring. Overbevisningen om at overvinde enhver forhindring er forbundet med begejstring, når rotorerne drejer lønsomt rundt.","b2":"Netværk","t9":"Det bedste til dig","t10":"Vi hævder ikke, at vi kan alting bedre. Men vi siger, at vi for vores kunder får det allerbedste ud af maskiner, teknologier, kontrakter, lovgivning og ud af os selv. Vore team råder over kapaciteterne og kompetencerne til at lede et vindprojekt lige fra identifikation af området over finansiering til fremstilling og ibrugtagning, og til at sikre skræddersyede løsninger samt planlægningssikkerhed. I denne forbindelse hører repowering, som indebærer fornyelsen af gamle anlæg, også til blandt vores opgaver.","b3":"Team","t11":"","t12":"","t13":"","t14":"","t15":"","t16":"","t17":"","t18":"","t19":""}

