module.exports = {"t1":"Vores team\nEn for alle. Alle for dig!","t2":"Der er mange gode grunde til at vælge Greenwind, når det gælder projektudvikling. Hvert medlem af vores team er en god grund. Og samlet er vi mere end summen af de enkelte medarbejdere.","team":[{"name":"Martin Kühl","job":"Administrerende direktør","bio":"har arbejdet med vindenergi siden 1995 og er fascineret af vindens kraft.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140168/Martin_Kuehl_250_ffdbrw.jpg"},{"name":"Manuel Lasse","job":"Projektudvikling og Ibrugtagning\u2028","bio":"med fokus på det store billede og fremtiden.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140168/Manu_Lasse_250_l31s4h.jpg"},{"name":"Franziska Göbel","job":"Projektudvikling","bio":"med udholdenhed og kreativitet til en succesfuld godkendelse.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140168/Franziska_Goebel_250_ny5ifm.jpg"},{"name":"Christian Kandora","job":"Projektudvikling","bio":"med erfaring og engagement i vindparkens optimale layout.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140168/Christian_Kandora_250_vrjajm.jpg"},{"name":"Anne Pfannes","job":"Projektfinansiering","bio":"med nøjagtighed og betydning for tal og nye ideer.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140168/Anne_Pfannes_250_j8xlvt.jpg"},{"name":"Dietmar Mahler","job":"Områdeudvikling","bio":"i personlig kontakt med menneskerne på stedet.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140168/Dietmar_Mahler_250_ndqaze.jpg"},{"name":"Bernd Müller","job":"Områdeudvikling","bio":"med drivkraft og engagement til at fremme landejernes interesser.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140169/Bernd_Mueller_250_mn6n8i.jpg"},{"name":"Ulrich Mertens","job":"Områdeudvikling","bio":"for at nå målene med at omdanne energisystemet.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140169/Ulrich_Mertens_250_cn82ak.jpg"},{"name":"Jasmin Horstkötter","job":"Bogholderi","bio":"med frisk vind og den rette indstilling.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140169/Jasmin_Horstkoetterr_250_dyfxl5.jpg"},{"name":"Michael Witschas","job":"Bogholderi","bio":"for en bedre fremtid.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140169/Michael_Witschas_250_b5e1xt.jpg"},{"name":"Iris Gärtner","job":"Team-assistent","bio":"for effektivitet – i jobbet og i energiudnyttelsen.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140169/Iris_Gaertner_250_vs9vuf.jpg"},{"name":"Ulf von Rahden","job":"Områdeudvikling","bio":"med vind og energi kan meget ændres.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140169/Ulf_von_Rahden_250_w3sb4g.jpg"},{"name":"Sophie Kolb","job":"Assistent for produktudvikling","bio":"altid nysgerrig og yderst engageret i miljøvenlig energi.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140170/Sophie_Kolb_250_qoyc5w.jpg"}]}

