module.exports = {"t1":"Borgerenergi","t2":"Vi rådgiver og følger lokale initiativer af overbevisning.\n\nSom erfaren serviceudbyder og fair partner står vi til rådighed for alle, der ikke kan finde vej i godkendelsesjunglen, ikke kan komme videre på halvvejen, mister pusten under projektudviklingsmaratonen eller ønsker at gå en enklere vej i deres projektudvikling af vindenergianlæg.\n\nVi ved mere om de forskellige finansielle andelsmodeller, om mulighederne for bæredygtig naturbeskyttelse og udligningsforanstaltninger, om effektiviteten ved de forskellige teknologier eller den bedste forretningsmæssige og tekniske driftsledelse.\n\nTal med os!","t3":"Windflächen","t4":"Udnyt\ndin jord ...","t5":"... med vindens kraft","t6":"Om det er en græsmark, agerjord eller skov - der er passende løsninger til næsten alle udpegede vindmøllesteder. Den skånsomme opbygning og den tekniske videreudvikling af højmoderne anlæg gør mange steder lukrative. Vores planlæggere kender mulighederne, rådgiver landejere, kommuner og borgere og ved alt om bygge- og afstandsområder, samt om kabellednings- og anlægsrettigheder.\n\nEt vindenergianlæg på din ejendom kan give dig et finansielt perspektiv og yde et bidrag til beskyttelse af klimaet.","sections1":[{"title":"Diverse anvendelsesområder","p":"Til installeringen af et vindenergianlæg er der brug for forskellige områder, i første omgang selvfølgelig området til at bygge anlægget. Over det hele er der brug for de såkaldte afstandsarealer, hvis størrelse er fastlagt i lovgivningen. Desuden er der behov for jord til kabellednings- og anlægsrettigheder. Hvis du derfor gerne vil undersøge en mulig arealanvendelse, rådgiver vi dig gerne."},{"title":"Indtægtskilde for mange","p":"Ved enhver form for arealanvendelse modtager landejeren - uanset om han er enkeltperson eller i et partnerskab - sikre vederlag på lang sigt. Vi vælger sammen med dig den mest hensigtsmæssige forpagtningsmodel. Også naboer kan drage fordel heraf, hvis deres jord ligeledes kommer i betragtning til arealanvendelse. Kommunen opnår desuden indtægter fra erhvervsskatter, som operatøren skal betale efter ibrugtagningen af anlægget/anlæggene."},{"title":"Vindmøllesteder i skoven","p":"Uanset om det er en græsmark, agerjord eller skov - i dag er der passende løsninger til (næsten) alle egnede vindmøllesteder. Den skånsomme opbygning og den tekniske videreudvikling af højmoderne anlæg gør mange steder, som tidligere ikke kom i betragtning, mulige og lukrative."}],"t9":"Projektudvikling","t10":"Kloge kommuner, borgere, institutioner, landejere og investorer forstår fordelene ved decentral forsyning med vindkraft. Green Wind Energy udvikler vindparker i regionen i eget regi og sammen med partnere - til fordel for mange. Vore team råder over kapaciteterne og kompetencerne til at lede et vindprojekt lige fra identifikation af området over finansiering til fremstilling og ibrugtagning, og til at sikre skræddersyede løsninger samt planlægningssikkerhed. I denne forbindelse hører repowering, som indebærer fornyelsen af gamle anlæg, også til blandt vores opgaver.","sections2":[{"title":"Planlægning og koordinering","p":"I forlængelse af en områdesikring overtager vi alle planlægnings- og koordineringsprocesser med kommunen og regionsplanlægningen. Vi taler og forhandler med institutioner og investorer, tager os af godkendelser, udtalelser, fremstillingen, netforbindelsen samt den efterfølgende driftsledelse af vindenergianlægget.\n\nDet er vores mål at skabe en win-win-situation til gavn for alle parter. Først da opstår der en accept, som er afgørende for en velfungerende drift. Dertil hører optimale (brugs-)kontrakter, fornuftige finansierings- og forpagtningsmodeller, tilpassede kompenserende projekter samt natur- og artsbeskyttelsestiltag.\n\nRealiseringen af et vindprojekt er en kompleks opgave. Det varer noget tid, før rotorbladene drejer lønsomt rundt. Som fremtidig operatør af vindparker vil du altid have en kontaktperson, som gennemfører processerne transparent og åbent, og som rådgiver dig proaktivt og målrettet."},{"title":"Til gavn for alle","p":"Mange drager fordel af et miljøbevidst og sikkert projekteret vindenergianlæg: Investorer, som gerne vil opnå lukrative afkast med en fornuftig investering, kommuner, som med indtægter fra erhvervsskatter kan forpligte sig i nye projekter, eller også landejere, som bortforpagter deres jord til fremstillingen af et anlæg.\n\nVores økonomiansvarlige kontrollerer ved begyndelsen af enhver projektering, om det kan betale sig at realisere. Pålideligt forventede indtægter fra vederlag står over for udførlige omkostningsberegninger, som indeholder optimerede vedligeholdelses- og forpagtningskontrakter, driftsomkostninger og direkte levering. Vi udvikler skræddersyede finansieringsmodeller og bringer udvalgte partnere, institutioner og banker sammen på det rigtige tidspunkt."},{"title":"One stop supplier","p":"Opbygningen og driften af en vindpark, samt af et enkelt vindenergianlæg, kræver planlægning og logistik på højeste niveau og skal gå hånd i hånd med de pågældende producenter og tjenesteydere. Den nøglefærdige realisering indeholder udvalget af anlæg samt fremstillingen, ibrugtagningen og ikke mindst nettilslutningen. For vindenergianlæg er kraftværker. De skal opfylde alle krav om nettilslutning og integrering i forsyningsnettet. Det er også vores opgave at gennemføre dette i henhold til alle lovbestemmelser i den tyske lov om fremme af vedvarende energi (EEG), i overensstemmelse med krav fra netoperatøren, producenten og operatøren, samt med henblik på fremtidige netkoncepter."}],"t11":"Projektindkøb","t12":"Green Wind Energy er interesseret i alle vindprojekter på alle udviklingsstadier, ikke kun som rådgiver, men også som aktiv og fair partner. Vi tilbyder dig et tillidsfuldt samarbejde. Vi byder ind med alle vores kompetencer, erfaringer og al vores energi for at nå et godt og udbytterigt resultat for alle parter.","sections3":[{"title":"Projekter inden idrifttagning","p":"Hvad enten du er jordbesidder eller projektudvikler, kan vi kontrollere og/eller overtage dit projekt i mange forskellige faser, f.eks. når\n\n• alle arealer er sikret,\n• alle ansøgninger er indsendt,\n• ansøgningerne står umiddelbart foran godkendelse\n\nog grundlæggende, hvis projektudviklingen – af en eller anden grund – ikke har kunnet afsluttes ordentligt."},{"title":"Projekter i drift","p":"Hvis du som driftsansvarlig eller investor har gamle vindenergianlæg i din portefølje, kan vi undersøge og/eller overtage dine uønskede projekter, f.eks.\n\n• som ejer eller som delvis ejer,\n• i mange forskellige andele,\n• med eller uden repowering-potentiale,\n• i mange forskellige størrelser,\n\nog grundlæggende, hvis du – af en eller anden grund – overvejer at trække dig ud af dit projekt.\n\nExit: Slip af med gamle investeringer!","link":"repowering"}],"t13":"Repowering","t14":"... fra gammelt til nyt","t15":"Den teknologiske udvikling af vindenergianlæg har i de seneste år gjort enorme fremskridt - både hvad angår effektivitet, bæredygtighed og muligheden for at anvende mange forskellige vindmøllesteder bedst muligt. Repowering kan her hurtigt tjene sig ind. Green Wind Energy styrer proaktivt og målrettet processer for repowering.","t16":"Kontakt:","t17":"repowering@greenwindenergy.de","sections4":[{"title":"Forlængelse af løbetiden","p":"For mange vindprojekter, som blev opbygget for mere end ti år siden, betaler det sig i dag med repowering - hvilket indebærer, at et ældre og mindre anlæg erstattes med et nyt. Løbetiden for et eksisterende projekt kan dermed forlænges med yderligere 20 år og effektiviteten øges med det tre- til femdobbelte."},{"title":"Kan repowering betale sig?","p":"Om repowering rent faktisk betaler sig, kortlægger vi i begyndelsen af processen. Før når alle oplysninger ligger på bordet, skal operatøren beslutte sig. Vi hjælper så med at gennemføre repowering-tiltaget - lige fra forundersøgelsen og godkendelsesprocedurerne over gennemførelsen til driften - samt den tekniske og økonomiske driftsledelse, som udføres af vores partnervirksomhed Green Wind Operations."}],"t22":"","t23":"","t24":"","t25":"","t26":"","t27":"","t28":"","t29":"","t30":"","t31":"","t32":""}

