module.exports = {"t1":"Effektivitet og engagement på stedet","t2":"Green Wind Energy udvikler lokale vindenergiprojekter i og for regionen. Vi er kontaktpersonen, når det drejer sig om tætheden i eksisterende parker, repowering eller også om nyudviklinger med samarbejdspartnere. Se her et af de største repoweringprojekter 2017 i Tyskland og andre eksempler på projekter i Brandenburg.","sections":[{"title":"Windpark repowering","subsections":[{"title":"Projekt Vormark/Prignitz (Groß Pankow)","text":"I slutningen af 2016 afmonterede Green Wind Energy 14 eksisterende gamle anlæg i det egnede vindområde. På samme sted blev der opført 14 nye vindenergianlæg, der blev taget i drift i første kvartal 2017, inkl. egen transformerstation i Kuhsdorf til tilslutning af vindenergianlæggene.\n\nNominel effekt 48,3 megawatt\n\nOpførelsen af de nye vindenergianlæg med en navhøjde på 137 meter og en nominel effekt på 3.450 kilowatt (tidligere 1.500 kilowatt) blev udført inden for tre måneder. Ud af en nominel effekt på 21 megawatt opstod der dermed 48,3 megawatt. Energiudbyttet bliver ca. 4 gange større i kraft af repowering. Vindparken er udstyret med passende natmærkning.\n\nUdligningsforanstaltninger\n\nSom udligningsforanstaltninger blev der efter ønske fra kommunen revet bygninger ned, der ikke mere var nødvendige, der blev plantet træer og anlagt frugtplantager. Markfirben blev flyttet til nye habitater. På tre steder fik flagermusene nye kasser, og der blev hængt reder op til svalerne.\n\nProjektpartner: European Energy\nTeknisk driftsledelse: Green Wind Operations","img":"http://res.cloudinary.com/greenwind/image/upload/c_scale,w_800/v1505147660/GWE_Referenzen_037C9H1B2821_tujamd.jpg","video":"https://player.vimeo.com/video/233281688?color=000000&title=0&byline=0&portrait=0"},{"title":"Projekt Timpberg","text":"Sted: Zehdenick, Prignitz-Oberhavel, Brandenburg\nAnlæg gamle: 2 x VESTAS V80, 4 MW kapacitet, 78 m navhøjde, ibrugtagning i 2002\nNedlæggelse: 08/2015\nAnlæg nye: 2 x VESTAS V112, 6 MW kapacitet, 125 m navhøjde, ibrugtagning 12/2015\nBest: profitabel exit for ejeren af de gamle vindenergianlæg\u2028attraktivt projekt for den nye operatør\u2028vedvarende støtte fra befolkningen\u2028Green Wind Energy som totalentreprenør","img":""}]},{"title":"Vindpark komprimering","subsections":[{"title":"Projekt Vormark/Prignitz (Groß Pankow)","text":"Første udvidelse.\nSted: Groß Pankow, Prignitz-Oberhavel, Brandenburg\nAnlæg: 2 x VESTAS V80, 4 MW kapacitet, 78 m\nnavhøjde\nIbrugtagning: Første del af den trinvise omlægning af hele vindparken (se ovenfor)\nBest: stærk privat og offentlig støtte, Green Wind Energy som totalentreprenør","img":""}]},{"title":"Samarbejde nyudvikling","subsections":[{"title":"Projekt Klein-Mutz","text":"Sted: Zehdenick, Prignitz-Oberhavel, Brandenburg\nAnlæg: 4 x VESTAS V90, 8 MW kapacitet, 125 m navhøjde\nIbrugtagning: 12/2012\n\nProjektpartner: Saxovent, Notus\nBest: konstruktivt samarbejde, kort projektløbetid til godkendelse og gennemførelse","img":"http://res.cloudinary.com/greenwind/image/upload/c_scale,q_50,w_1052/v1505994982/AIMG_0794_gr_x1eggg.jpg"}]}]}

