module.exports = {"t1":"Med vindens kraft","t2":"... arbejder vi på, at energi, der udvindes på havet, strømmer effektivt, problemfrit og økonomisk i land. Så den er tilgængelig for alle! Hvert medlem af vores team stiller store krav til sin profession og går til arbejdet med fuld kraft. Det gør os stærke!","t3":"Manuel Lasse, diplomingeniør (FH)\ninden for miljøteknologi og vedvarende energi","t4":"administrerende direktør\nshar arbejdet for fuld kraft med vindenergi siden 2006.","t5":"Green Wind Offshore","t6":"Green Wind Offshore GmbH er en virksomhed inden for Green Wind Gruppe med hovedsæde i Berlin centrum. Som specialist inden for vedligeholdelsesstyring og overvågning af tekniske anlæg til energiproduktion offshore er vi også aktive i Rostock og i området ved Emden/Wilhelmshaven.\n\nVi opererer selvstændigt og uafhængigt på hele det tyske og det internationale marked. Vi yder fuld støtte inden for teknisk driftsledelse af offshore-vindenergianlæg og inden for platformsstyring til HVDC og transformerstationer. Vi arbejder med fokus på samarbejde og partnerskab og har i mange år været fast forankret i vindenergien.\n  Certificeret iht. ISO 9001:2015.","b1":"Ydelser","t7":"Ny viden om vind","t8":"Green Wind Offshore er ny viden om vinden kombineret med erfaring og overbevisningen om at komme med på enhver bølge. Det er begejstring, når et hjul passer ind i de andre, når planlægning, handling og organisering problemfrit samles til et stort hele, og hele indsatsen bliver til ren energi.","b2":"Netværk","t9":"Det bedste til dig","t10":"Vi hævder ikke, at vi kan alting bedre. Men vi siger, at vi for vores kunder får det allerbedste ud af maskiner, materialer, teknologier, kontrakter, lovgivning og ud af os selv. Vi har et fintmasket netværk og en lokal tilstedeværelse i aktiviteterne i Nord- og Østersø-regionen. Vores kunder kan regne med korte veje og en direkte kontaktperson.","b3":"Team"}

