module.exports = {"t1":"Hovedkontrolcenter","t2":"Effektiv og koordineret overvågning.","t2b":"Hele projektledelsen for store offshore-projekter befinder sig for det meste i vores kontrolcentre 24/7.","sections0":[{"title":"Kontrolcentre tæt på","p":"Hovedkontrolcentret og hjertet ligger i Berlin. Herfra indsætter vi kontrolcentre i umiddelbar nærhed af projekternes placering, som f.eks. ved vedligeholdelsesstyringen af nettilslutningssystemet DolWin3. Hvad enten det drejer sig om bortskaffelse af affald og spildevand, reservedelsforsyning og -styring eller Technical Support Line (TSL) – her dokumenterer, systematiserer, overvåger og kontrollerer vores eksperter hele anlægsdriften og -tilstanden."},{"title":"Overvågning døgnet rundt","p":"På den måde kan alle indsatsgrupper og deres aktiviteter styres optimalt og forsynes via den eksisterende lagerlogistik med de nødvendige reservedele og de relevante værktøjer. Alle de vigtigste processer overvåges og koordineres centralt – 24 timer i døgnet, syv dage om ugen. Effektivt og sikkert."}],"t3":"","t4":"Planlaegning","t5":"Nye koncepter udtænkes.","t6":"Udgangspunktet for vores tekniske styring er devisen: Maksimer driftstiden, og nedsæt omkostningerne.\n\nVedligeholdelsesstyring og teknisk driftsledelse har en stor betydning for, hvor stor den økonomiske succes for offshore-havmølleparker bliver i fremtiden.\n\nVores sofistikerede koncept starter med en omhyggelig forberedelsesfase. I koordination med ordregivere, værfter, konstruktører og ingeniører skaber vi sikkerhed i planlægningen.","sections1":[{"title":"Fremadskuende og systematisk","p":"Med til vores styrker hører en fleksibel og fremadskuende tankegang, præcision i planlægningen, oversigt over kampagner og strukturer, fuldstændig dokumentation for materialer og processer, systematisk støtte til vedligeholdelsesprocesser via anvendelse af software (CMMS)."},{"title":"For alle store offshore-projekter","p":"Det af Green Wind Offshore nyudviklede vedligeholdelseskoncept for vindenergianlæg, platforme, kabler til lands og til vands, transformerstationer og koblingsudstyr kan overføres til store projekter af samme type i Nord- og Østersøen og langs de engelske og franske kyster."}],"t9":"Management","t10":"","sections2":[{"title":"Hurtig og direkte handling.","p":"Vores måde at handle på er bestemt af korte reaktionstider og målet om at undgå stilstand og øge effektiviteten.\n\nBrugen af den nyeste teknik giver maksimal tilgængelighed for anlæg og de højeste sikkerhedsstandarder for personer og anlæg. En certificeret offshore-uddannelse af vores medarbejdere er en selvfølge. Høj motivation og engagement øger kvaliteten. Hertil kommer også: flade hierarkier og hurtige beslutningsprocesser samt samarbejdsvillighed og fokus på partnerskab. Store offshore-projekter har ikke brug for individualister, men for førsteklasses teamplayere."},{"title":"Til lands og til vands","p":"Med til vores måde at handle på hører også organisering og koordinering af alle vedligeholdelsesforanstaltninger, der kan planlægges, ikke-planlagt vedligeholdelsesarbejde og fejlafhjælpning samt yderligere vedligeholdelsesforanstaltninger til lands og til vands."},{"title":"Kontrolcentre og logistikbaser","p":"Takket være vores kontrolcentre, logistikbaser, det fleksible personale og de effektive partnere har Green Wind Offshore muligheden for at integrere yderligere HVDC-platforme, offshore-transformerstationer og offshore-vindenergianlæg i eksisterende vedligeholdelsesstrukturer og evt. for at udvide dem. Vi integrerer enkelte systemer og aflaster dermed vores kunder."},{"title":"Managementsystemer","p":"Vores vigtigste mål inden for kvalitetsstyring er en struktureret intern organisation samt et kooperativt og vellykket samarbejde med kunder og leverandører. Dette omfatter ligeledes overholdelse af tidsfrister og budgetter. Men også områderne sikkerhed, arbejdsbeskyttelse og miljø er underlagt den kontinuerlige kontrol. Vi arbejder kontinuerligt på alle systemer, som","list":["Kvalitetsstyringssystem (QMS) iht. ISO 9001","IT-sikkerhedsstyringssystem iht. ISO 27001","Miljøstyringssystem (UMS) iht. ISO 14001","Asset-styringssystem iht. ISO 55001","Arbejdsbeskyttelsesstyringssystem og HSE osv. integreret i 9001 (QMS) og 14001 (UMS) samt OHSAS ISO 18001","Energistyringssystem iht. ISO 50001"]}],"t11":"Logistik","t12":"","sections3":[{"title":"Fleksibel og præcis organisering.","p":"Med henblik på høj tilgængelighed og optimerede arbejdsprocesser skal logistik og reservedelsforsyning i den tekniske styring altid stemme."},{"title":"Kystnære placeringer","p":"På havet kan blot én manglende skrue betyde unødvendigt besvær. For at reservedelsstyringen kan klare både forsyning og bortskaffelse af forbrugs- og driftsstoffer så hurtigt som muligt, installerer og administrerer vi kystnære lokaliteter inklusive reservedelslager med manpower og via CMMS. Den sikre offshore-transport af materialer og personer og anbringelsen af dem hører ligeledes med til vores opgaver ligesom transport fra oplagringssteder til omskibningshavne og transformerstationer på land."},{"title":"Forebyggende og korrigerende","p":"Alle store projekter har deres egne særlige krav, som vi reagerer fleksibelt på. Forebyggende og korrigerende vedligeholdelseskampagner kræver en præcis indsats fra teknikere til lands og til vands."}]}

