module.exports = {"t1":"Med vindens kraft","t2":"... finder vi løsninger til de fleste problemer og tager alt op med optimisme, som drejer rundt eller (endnu) ikke drejer. Vi løber ikke tør for nye ideer. Mit team og jeg har gentagne gange bevist, at vi overvinder (næsten) alle forhindringer og kommer videre som mange andre. Vindenergi - det er mere for mig end blot et job.","t3":"Manuel Lasse","t4":"Administrerende direktør\nDiplomingeniør (FH) inden for miljøteknologi og vedvarende energi, har arbejdet med vindenergi siden 2006","t5":"Green Wind Operations","t6":"... er en vindenergivirksomhed med hovedsæde i Berlin centrum og projekter i hele Tyskland. Vi opererer selvstændigt og uafhængigt. Vi giver fuld støtte til teknisk og økonomisk driftsledelse til private og institutionelle operatører af vindparker. Vi arbejder med fokus på samarbejde og partnerskab og har i mange år været fast forankret i vindenergien. Certificeret iht. ISO 9001:2015.","b1":"Ydelser","t7":"Ny viden om vind","t8":"Hos Green Wind Operations kombineres ny viden om vind med mange års erfaring. Overbevisningen om at overvinde enhver forhindring er forbundet med begejstring, når rotorerne drejer lønsomt rundt.","b2":"Netværk","t9":"Det bedste til dig","t10":"Vi hævder ikke, at vi kan alting bedre. Men vi siger, at vi for vores kunder får det allerbedste ud af maskiner, teknologier, kontrakter, lovgivning og ud af os selv. Vores team råder over kapaciteterne og kompetencerne til døgnet rundt at lede vindenergianlæg bedst muligt, efter de er taget i brug.","b3":"Team","t11":"Følger udviklingen med at omdanne energisystemet","t12":"Vi er altid velinformeret takket være vores sagkyndige medarbejdere og vores placering i Berlin - direkte i det politiske centrum for omdannelsen af energisystemet. Om det er repowering, handel med elektricitet, lovmæssige grundlag, opdateringer eller fornyelser til stabilisering af forsyningsnettet inden for området af den tyske lov EEG om fremme af vedvarende energi - vi rådgiver dig aktivt og giver vores anbefalinger.","b4":"Kontakt","t13":"","t14":"","t15":"","t16":"","t17":"","t18":"","t19":""}

