module.exports = {"t1":"Hovedkontrolcenter døgnet rundt","t2":"Alle processer inden for den tekniske driftsledelse af vindenergianlæg og transformerstationer vises hovedsageligt i vores kontrolcentre 24/7. Hovedkontrolcentret og hjertet ligger i Berlin. Vi dokumenterer, systematiserer, overvåger og kontrollerer anlæggenes samlede drift og tilstand. På den måde kan vores indsatsgrupper og deres aktiviteter styres optimalt, ligesom de effektivt kan være til stede, hvor der er brug for det. Alle de vigtigste processer overvåges og koordineres centralt – 24 timer i døgnet, syv dage om ugen.\n\nEffektivt og sikkert.","t3":"","t4":"Teknisk driftsledelse","t5":"","t6":"","sections1":[{"title":"Kontrol af forløb","p":"Vi besigtiger regelmæssigt anlæggene - lige fra kabinen til maskinhuset - og kontrollerer og dokumenterer minutiøst, om kontrolintervaller og vedligeholdelsesopgaver overholdes. Dertil hører også den halvårlige inspektion af hele vindenergianlægget. Vores kunder er altid fuldt informerede om alle processer takket være kundens egen webadgang, individuel udarbejdelse af rapporter og personlig rapportering."},{"title":"Helt præcis","p":"Først når det tekniske arbejde på stedet, analysen af data, udtalelser og besigtigelser, indhentningen af tilbud samt den detaljerede undersøgelse af et objekt bliver samlet og sagkyndigt forbundet, opnår vi optimale resultater. Først da udvikler teknisk driftsledelse sin fulde kraft. Derfor koordinerer Green Wind Operations også eksterne driftsledere i hele Tyskland og i nabolandene, skaber fælles strukturer og samler alle informationsstrømme."},{"title":"Overvåget døgnet rundt","p":"Til dataovervågningen bruger vi det kendte program ROTORSOFT fra firmaet Drehpunkt samt softwaren WIS fra firmaet Softenergy, dvs.:\n\n• 24 timers live-overvågning af vindenergianlæggene\n• Software-baseret grænseflade til den økonomiske driftsledelse\n• ideelle betingelser til handel med elektricitet\n\nDen individuelle udarbejdelse af rapporter er tilpasset din portefølje med måneds- og årsrapporter. Derudover udarbejder vi økonomiske driftsledelsesrapporter."},{"title":"Overvågning af eksterne ydelser","p":"Tillid er godt, kontrol er bedre! Eksterne tjenesteyderes arbejde bliver kontrolleret løbende, og kontraktmæssige aftaler og afregninger bliver gennemgået. Vi organiserer og gennemfører supplerende bestemmelser i henhold til den tyske lov om beskyttelse mod emissioner 'BiMSchG'"}],"t9":"Økonomisk driftsledelse","t10":"Økonomisk driftsledelse hos Green Wind vil sige at sikre altid strømmende finansielle bevægelser og udelukke alle forstyrrende faktorer og unødvendige omkostninger for at opnå det bedst mulige afkast. Vi kan allerede på forhånd kontrollere hele porteføljen for så vidt angår eksisterende kontrakter og komme med forslag til en fornuftig kontraktstruktur, f.eks. til optimering af vedligeholdelseskontrakter.","sections2":[{"title":"Optimering af oplysninger","p":"Selve kernen i en økonomisk driftsledelse er et klippefast bogholderi og herunder afviklingen af betalinger. Desuden optimerer vores økonomiansvarlige den regnskabsmæssige proces, således at den er skræddersyet til driften af hver enkelt anlæg, tilpasset de specifikke finansieringsstrukturer og de skattemæssige faktorer. Vores medarbejdere beregner den bedste effektivitet for anlægget og tager sig af en gnidningsfri afregning samt fordeling af indtægter fra leveringer."},{"title":"Planlægning og styring af processer","p":"Til planlægning og styring af disse processer anvender vi vores nye viden om vind, hvad angår likviditets- og budgetplanlægning, afregning af driftsforstyrrelser i netsupporten, direkte levering, commercial due diligence, etc. Og alt er baseret på den løbende kontakt til kunden."}],"t11":"Repowering eller optimering","t12":"Den teknologiske udvikling af vindenergianlæg har i de seneste år gjort enorme fremskridt - både hvad angår effektivitet, bæredygtighed og muligheden for at anvende mange forskellige vindmøllesteder bedst muligt. Vores driftsledere kender anlæggene fuldt ud og laver forslag til driftsoptimeringer eller også repowering-tiltag.","sections3":[{"title":"Yderligere 20 års løbetid","p":"For mange vindprojekter, som blev opbygget for mere end ti år siden, betaler det sig med repowering - hvilket indebærer, at et ældre og mindre anlæg erstattes med et nyt. Løbetiden for et eksisterende projekt kan dermed forlænges med yderligere 20 år og effektiviteten øges med det tre- til femdobbelte. Så kan en investering hurtigt tjene sig ind."},{"title":"Kan repowering betale sig?","p":"Vi betragter det som vores opgave, at vi til vores kunder proaktivt og aktivt laver forslag til repowering og på kompetent vis kortlægger, om repowering rent faktisk betaler sig. Før når alle oplysninger ligger på bordet, skal operatøren beslutte sig."},{"title":"Projektering med partner","p":"Vores partnervirksomhed Green Wind Energy GmbH hjælper med at gennemføre repowering-tiltag - lige fra forundersøgelse og godkendelsesprocedurer over gennemførelse til drift. Green Wind Energy har i flere år udviklet vindparker i regionen sammen med partnere og styrer processer for repowering. Hvis det ønskes, kan Green Wind Operations GmbH derefter gnidningsløst overtage den tekniske og økonomiske driftsledelse med den sædvanlige kvalitet."}]}

