module.exports = {"t1":"Our team\nOne for all. All for you!","t2":"There are many good reasons for deciding in favour of Greenwind’s project development. Each member of our team of a good reason. And all together, we are more than the sum of individuals.","team":[{"name":"Martin Kühl","job":"Company’s Management","bio":"has been engaged in the wind power business since 1995 and is fascinated by the power of wind.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140168/Martin_Kuehl_250_ffdbrw.jpg"},{"name":"Manuel Lasse","job":"Project development and commissioning","bio":"with an eye for the big picture and a view of the future.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140168/Manu_Lasse_250_l31s4h.jpg"},{"name":"Franziska Göbel","job":"Project development","bio":"with perseverance and creativity for successful approval.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140168/Franziska_Goebel_250_ny5ifm.jpg"},{"name":"Christian Kandora","job":"Project development","bio":"with the experience and dedication to optimise wind-farm layouts.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140168/Christian_Kandora_250_vrjajm.jpg"},{"name":"Anne Pfannes","job":"Project financing","bio":"with a meticulous sense for figures, and new ideas.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140168/Anne_Pfannes_250_j8xlvt.jpg"},{"name":"Dietmar Mahler","job":"Location development","bio":"in personal contact with the people on-site.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140168/Dietmar_Mahler_250_ndqaze.jpg"},{"name":"Bernd Müller","job":"Location development","bio":"with enthusiasm and commitment for the  interests of the land owners.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140169/Bernd_Mueller_250_mn6n8i.jpg"},{"name":"Ulrich Mertens","job":"Location development","bio":"so that the objectives of the energy transition are achieved.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140169/Ulrich_Mertens_250_cn82ak.jpg"},{"name":"Jasmin Horstkötter","job":"Accountancy","bio":"with a fresh outlook and the appropriate attitude.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140169/Jasmin_Horstkoetterr_250_dyfxl5.jpg"},{"name":"Michael Witschas","job":"Accounting","bio":"for a future worth living.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140169/Michael_Witschas_250_b5e1xt.jpg"},{"name":"Iris Gärtner","job":"Team Assistant","bio":"for efficiency – at work and in energy use.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140169/Iris_Gaertner_250_vs9vuf.jpg"},{"name":"Ulf von Rahden","job":"Location development","bio":"transformation with wind and energy.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140169/Ulf_von_Rahden_250_w3sb4g.jpg"},{"name":"Sophie Kolb","job":"Project Development Assistant","bio":"always curious and totally committed to environmentally friendly energy.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140170/Sophie_Kolb_250_qoyc5w.jpg"}]}

