module.exports = {"t1":"Efficiency and commitment on-site","t2":"Green Wind Energy develops wind-power projects on-site in, and for the region. We are the people you contact regarding the consolidation of existing farms, repowering and also new-developments with cooperation partners. Here you can see one of Germany´s biggest repowering projects 2017 and other model projects in Brandenburg.","sections":[{"title":"Wind farm repowering","subsections":[{"title":"Project Vormark/Prignitz (Groß Pankow)","text":"In late 2016 Green Wind Energy removed 14 old installations at wind suitable sites and replaced them with 14 new wind turbines. They were put into operation in the first quarter 2017 including a connecting substation in Kuhsdorf.\n\nRated power 48.3 megawatts\n\nThe construction of the new wind turbine with an axle-shaft height of 449.48 feet and a rated power of 3,450 kilowatts (before: 1,500 kilowatts) took place within three months. 21 megawatts rated power were turned into 48.3 megawatts. Repowering increased the energy yield by around 4 times. The wind farm is equipped with appropriate night identification.\n\nCompensatory measures\n\nUpon request of the municipality, no longer required buildings were demolished as compensatory measures, trees were planted and a traditional orchard was laid. Sand lizards were relocated in surrogate habitats. Bats received new nest boxes at three spots and nests were also placed for barn swallows.\n\nProject partner: European Energy\nTechnical operating management: Green Wind Operations","img":"http://res.cloudinary.com/greenwind/image/upload/c_scale,w_800/v1505147660/GWE_Referenzen_037C9H1B2821_tujamd.jpg","video":"https://player.vimeo.com/video/233281688?color=000000&title=0&byline=0&portrait=0"},{"title":"Project Timpberg","text":"Site location: Zehdenick, Prignitz-Oberhavel, Brandenburg\nOld installations: 2 x VESTAS V80, 4 MW power, 78 m hub height, commissioned 2002\nDismantled: 08/2015\nNew installations: 2 x VESTAS V112, 6 MW power, 125 m hub height, commissioning 12/2015\nBest: profitable exit for the owner of the old wind power plants, attractive project for the new operator, continued support by the general public, Green Wind Energy as the general contractor","img":""}]},{"title":"Wind farm compression","subsections":[{"title":"Project Vormark/Prignitz (Groß Pankow)","text":"First enhancement of the Prignitz wind-farm\nsite location: Groß Pankow, Prignitz-Oberhavel, Brandenburg\nAnlagen: 2 x VESTAS V80, 4 MW power, 78 m\nhub height\nCommissioned: 2011 first part of the multiphase new planning of the entire wind-farm, s.o.\nBest: strong private and public support, Green Wind Energy as the general contractor","img":""}]},{"title":"Co-operation new development","subsections":[{"title":"Project Klein-Mutz","text":"Site location: Zehdenick, Prignitz-Oberhavel, Brandenburg\nAnlagen: 4 x VESTAS V90, 8 MW power, 125 m hub height\nCommissioned: 12/2012\n\nProject partner: Saxovent, Notus\nBest: constructive collaboration, short term project for approval and implementation","img":"http://res.cloudinary.com/greenwind/image/upload/c_scale,q_50,w_1052/v1505994982/AIMG_0794_gr_x1eggg.jpg"}]}]}

