module.exports = {"t1":"Locations","t2":"","sections":[{"title":"","subsections":[{"title":"Green Wind regional office","text":"Technical management at the Gross Pankow site. Our Green Wind regional office monitors an overall of 180 megawatts in the Prignitz, including Green Wind´s repowering project wind farm Vormark/Prignitz.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505155131/GW_Karte_2017_xxkyhz.svg"}]}],"t7":"","t8":"","t9":"","t10":"","t11":"","t12":"","t13":"","t14":"","t15":"","t16":"","t17":"","t18":"","t19":"","t20":"","t21":"","t22":"","t23":"","t24":"","t25":"","t26":"","t27":"","t28":"","t29":"","t30":"","t31":"","t32":""}

